<template>
  <div><input type="checkbox" checked v-if="isChecked"></div>
  <div><input type="checkbox" v-if="isChecked"></div>
</template>

<script>
export default {
  setup(props) {
    let { isChecked } = false;
    if (props.params.value == '1')
    {
        isChecked = true;
    }
    return {
      isChecked
    };
  }
}
</script>

<style scoped>
input {
  width: 20px;
  height: 20px;
}
div {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 80px;
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
</style>